import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import { v4 as uuidv4 } from 'uuid'
import type { 
  Appointment, 
  AppointmentStatus,
  CreateAppointmentRequest,
  PaymentStatus
} from '@/lib/calendar/types'

// ==========================================
// GET /api/v1/appointments - List appointments
// ==========================================
export async function GET(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const subAccountId = searchParams.get('subAccountId')
    const appointmentId = searchParams.get('id')
    const calendarId = searchParams.get('calendarId')
    const contactId = searchParams.get('contactId')
    const assignedTo = searchParams.get('assignedTo')
    const startDate = searchParams.get('startDate')
    const endDate = searchParams.get('endDate')
    const status = searchParams.get('status') as AppointmentStatus | null
    const page = parseInt(searchParams.get('page') || '1')
    const limit = Math.min(parseInt(searchParams.get('limit') || '20'), 100)
    const sort = searchParams.get('sort') || 'start_time_asc'

    if (!subAccountId) {
      return NextResponse.json(
        { success: false, error: 'subAccountId is required' },
        { status: 400 }
      )
    }

    // If specific appointment requested
    if (appointmentId) {
      const { data: appointment, error } = await supabase
        .from('appointments')
        .select(`
          *,
          calendars (*),
          appointment_types (*),
          contacts:contact_id (id, first_name, last_name, email, phone)
        `)
        .eq('id', appointmentId)
        .single()

      if (error || !appointment) {
        return NextResponse.json(
          { success: false, error: 'Appointment not found' },
          { status: 404 }
        )
      }

      // Verify it belongs to the sub-account
      if (appointment.calendars?.sub_account_id !== subAccountId) {
        return NextResponse.json(
          { success: false, error: 'Appointment not found' },
          { status: 404 }
        )
      }

      return NextResponse.json({
        success: true,
        data: transformAppointment(appointment)
      })
    }

    // Build query
    let query = supabase
      .from('appointments')
      .select(`
        *,
        calendars!inner (id, name, sub_account_id),
        appointment_types (id, name)
      `, { count: 'exact' })
      .eq('calendars.sub_account_id', subAccountId)

    if (calendarId) {
      query = query.eq('calendar_id', calendarId)
    }

    if (contactId) {
      query = query.eq('contact_id', contactId)
    }

    if (assignedTo) {
      query = query.eq('assigned_to', assignedTo)
    }

    if (startDate) {
      query = query.gte('start_time', new Date(startDate).toISOString())
    }

    if (endDate) {
      query = query.lte('start_time', new Date(new Date(endDate).setHours(23, 59, 59, 999)).toISOString())
    }

    if (status) {
      query = query.eq('status', status)
    } else {
      // Default: exclude cancelled
      query = query.neq('status', 'cancelled')
    }

    // Sorting
    if (sort === 'start_time_desc') {
      query = query.order('start_time', { ascending: false })
    } else {
      query = query.order('start_time', { ascending: true })
    }

    // Pagination
    const from = (page - 1) * limit
    const to = from + limit - 1
    query = query.range(from, to)

    const { data: appointments, error, count } = await query

    if (error) {
      console.error('Error fetching appointments:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to fetch appointments' },
        { status: 500 }
      )
    }

    const transformedAppointments = (appointments || []).map(transformAppointment)

    return NextResponse.json({
      success: true,
      data: transformedAppointments,
      pagination: {
        page,
        limit,
        total: count || 0,
        totalPages: Math.ceil((count || 0) / limit)
      }
    })

  } catch (error) {
    console.error('Error in GET /api/v1/appointments:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// POST /api/v1/appointments - Create appointment
// ==========================================
export async function POST(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      calendarId,
      appointmentTypeId,
      startTime,
      timezone,
      contact,
      additionalGuests,
      customAnswers,
      notes,
      sendConfirmation = true
    } = body as CreateAppointmentRequest

    // Validation
    if (!calendarId || !startTime || !timezone || !contact?.name || !contact?.email) {
      return NextResponse.json(
        { success: false, error: 'calendarId, startTime, timezone, and contact (name, email) are required' },
        { status: 400 }
      )
    }

    // Get calendar
    const { data: calendar, error: calError } = await supabase
      .from('calendars')
      .select('*')
      .eq('id', calendarId)
      .eq('status', 'active')
      .single()

    if (calError || !calendar) {
      return NextResponse.json(
        { success: false, error: 'Calendar not found or inactive' },
        { status: 404 }
      )
    }

    // Get appointment type if provided
    let duration = calendar.duration_minutes
    let appointmentType = null
    
    if (appointmentTypeId) {
      const { data: at } = await supabase
        .from('appointment_types')
        .select('*')
        .eq('id', appointmentTypeId)
        .eq('calendar_id', calendarId)
        .eq('is_active', true)
        .single()

      if (at) {
        appointmentType = at
        duration = at.duration_minutes
      }
    }

    // Calculate end time
    const startDateTime = new Date(startTime)
    const endDateTime = new Date(startDateTime.getTime() + duration * 60 * 1000)

    // Validate slot availability (simplified check)
    const { data: conflicts } = await supabase
      .from('appointments')
      .select('id')
      .eq('calendar_id', calendarId)
      .in('status', ['scheduled', 'confirmed'])
      .or(`start_time.lte.${endDateTime.toISOString()},end_time.gte.${startDateTime.toISOString()}`)

    if (conflicts && conflicts.length > 0) {
      return NextResponse.json(
        { success: false, error: 'Selected time slot is no longer available' },
        { status: 409 }
      )
    }

    // Generate tokens
    const confirmationToken = generateToken()
    const cancellationToken = generateToken()
    const rescheduleToken = generateToken()

    // Determine assigned user (for round-robin calendars)
    let assignedTo = null
    if (calendar.calendar_type === 'round_robin') {
      assignedTo = await getNextRoundRobinUser(supabase, calendarId)
    }

    // Find or create contact
    let contactId = null
    const { data: existingContact } = await supabase
      .from('contacts')
      .select('id')
      .eq('sub_account_id', calendar.sub_account_id)
      .eq('email', contact.email)
      .single()

    if (existingContact) {
      contactId = existingContact.id
    } else {
      // Create new contact
      const nameParts = contact.name.split(' ')
      const firstName = nameParts[0] || ''
      const lastName = nameParts.slice(1).join(' ') || ''

      const { data: newContact } = await supabase
        .from('contacts')
        .insert({
          id: uuidv4(),
          sub_account_id: calendar.sub_account_id,
          first_name: firstName,
          last_name: lastName,
          email: contact.email,
          phone: contact.phone,
          status: 'active',
          language: 'en'
        })
        .select('id')
        .single()

      contactId = newContact?.id
    }

    // Create appointment
    const appointmentId = uuidv4()
    const { data: appointment, error: aptError } = await supabase
      .from('appointments')
      .insert({
        id: appointmentId,
        calendar_id: calendarId,
        appointment_type_id: appointmentTypeId,
        start_time: startDateTime.toISOString(),
        end_time: endDateTime.toISOString(),
        timezone,
        duration_minutes: duration,
        assigned_to: assignedTo,
        contact_id: contactId,
        contact_name: contact.name,
        contact_email: contact.email,
        contact_phone: contact.phone,
        additional_guests: additionalGuests,
        status: calendar.auto_confirm ? 'confirmed' : 'scheduled',
        confirmation_status: calendar.auto_confirm ? 'confirmed' : 'pending',
        payment_status: appointmentType?.require_payment ? 'pending' : 'not_required',
        custom_questions_answers: customAnswers,
        notes,
        is_recurring: false,
        reschedule_count: 0,
        confirmation_token: confirmationToken,
        cancellation_token: cancellationToken,
        reschedule_token: rescheduleToken
      })
      .select()
      .single()

    if (aptError) {
      console.error('Error creating appointment:', aptError)
      return NextResponse.json(
        { success: false, error: 'Failed to create appointment' },
        { status: 500 }
      )
    }

    // Schedule reminders (placeholder - would be handled by Accelerator/Automation module)
    // await scheduleReminders(appointmentId, startDateTime)

    // Generate response with booking URLs
    const baseUrl = process.env.NEXT_PUBLIC_APP_URL || ''
    const bookingConfirmation = {
      id: appointmentId,
      calendarId,
      startTime: appointment.start_time,
      endTime: appointment.end_time,
      timezone,
      status: appointment.status,
      confirmationToken,
      cancelUrl: `${baseUrl}/cancel/${cancellationToken}`,
      rescheduleUrl: `${baseUrl}/reschedule/${rescheduleToken}`,
      meetingLink: appointment.meeting_link,
      calendarInvite: {
        google: generateGoogleCalendarUrl(appointment, calendar),
        outlook: generateOutlookUrl(appointment, calendar),
        icsUrl: `${baseUrl}/api/v1/appointments/${appointmentId}/ics`
      }
    }

    return NextResponse.json({
      success: true,
      data: bookingConfirmation
    }, { status: 201 })

  } catch (error) {
    console.error('Error in POST /api/v1/appointments:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// PUT /api/v1/appointments - Update appointment
// ==========================================
export async function PUT(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      id,
      status,
      confirmationStatus,
      meetingLocationType,
      meetingLocation,
      meetingLink,
      meetingPassword,
      notes,
      internalNotes,
      assignedTo,
      paymentStatus,
      paymentAmount,
      paymentTransactionId
    } = body

    if (!id) {
      return NextResponse.json(
        { success: false, error: 'id is required' },
        { status: 400 }
      )
    }

    // Build update object
    const updates: Record<string, any> = {
      updated_at: new Date().toISOString()
    }

    if (status !== undefined) updates.status = status
    if (confirmationStatus !== undefined) updates.confirmation_status = confirmationStatus
    if (meetingLocationType !== undefined) updates.meeting_location_type = meetingLocationType
    if (meetingLocation !== undefined) updates.meeting_location = meetingLocation
    if (meetingLink !== undefined) updates.meeting_link = meetingLink
    if (meetingPassword !== undefined) updates.meeting_password = meetingPassword
    if (notes !== undefined) updates.notes = notes
    if (internalNotes !== undefined) updates.internal_notes = internalNotes
    if (assignedTo !== undefined) updates.assigned_to = assignedTo
    if (paymentStatus !== undefined) updates.payment_status = paymentStatus
    if (paymentAmount !== undefined) updates.payment_amount = paymentAmount
    if (paymentTransactionId !== undefined) updates.payment_transaction_id = paymentTransactionId

    // Handle status changes
    if (status === 'cancelled') {
      updates.cancelled_at = new Date().toISOString()
    }

    const { data: appointment, error } = await supabase
      .from('appointments')
      .update(updates)
      .eq('id', id)
      .select()
      .single()

    if (error || !appointment) {
      console.error('Error updating appointment:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to update appointment' },
        { status: 500 }
      )
    }

    return NextResponse.json({
      success: true,
      data: transformAppointment(appointment)
    })

  } catch (error) {
    console.error('Error in PUT /api/v1/appointments:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// DELETE /api/v1/appointments - Cancel appointment
// ==========================================
export async function DELETE(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const id = searchParams.get('id')
    const reason = searchParams.get('reason')
    const notifyContact = searchParams.get('notifyContact') !== 'false'

    if (!id) {
      return NextResponse.json(
        { success: false, error: 'id is required' },
        { status: 400 }
      )
    }

    const { data: appointment, error } = await supabase
      .from('appointments')
      .update({
        status: 'cancelled',
        cancellation_reason: reason,
        cancelled_at: new Date().toISOString(),
        updated_at: new Date().toISOString()
      })
      .eq('id', id)
      .select()
      .single()

    if (error || !appointment) {
      console.error('Error cancelling appointment:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to cancel appointment' },
        { status: 500 }
      )
    }

    // TODO: Send cancellation notification if notifyContact is true
    // This would be handled by the Accelerator/Automation module

    return NextResponse.json({
      success: true,
      data: {
        id: appointment.id,
        status: 'cancelled',
        cancelledAt: appointment.cancelled_at
      }
    })

  } catch (error) {
    console.error('Error in DELETE /api/v1/appointments:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// HELPER FUNCTIONS
// ==========================================

function transformAppointment(dbAppointment: any): Omit<Appointment, 'calendar' | 'appointmentType'> & {
  calendar?: { id: string; name: string };
  appointmentType?: { id: string; name: string };
} {
  return {
    id: dbAppointment.id,
    calendarId: dbAppointment.calendar_id,
    appointmentTypeId: dbAppointment.appointment_type_id,
    startTime: dbAppointment.start_time,
    endTime: dbAppointment.end_time,
    timezone: dbAppointment.timezone,
    durationMinutes: dbAppointment.duration_minutes,
    assignedTo: dbAppointment.assigned_to,
    contactId: dbAppointment.contact_id,
    contactName: dbAppointment.contact_name,
    contactEmail: dbAppointment.contact_email,
    contactPhone: dbAppointment.contact_phone,
    additionalGuests: dbAppointment.additional_guests,
    status: dbAppointment.status,
    confirmationStatus: dbAppointment.confirmation_status,
    meetingLocationType: dbAppointment.meeting_location_type,
    meetingLocation: dbAppointment.meeting_location,
    meetingLink: dbAppointment.meeting_link,
    meetingPassword: dbAppointment.meeting_password,
    customQuestionsAnswers: dbAppointment.custom_questions_answers,
    notes: dbAppointment.notes,
    internalNotes: dbAppointment.internal_notes,
    cancellationReason: dbAppointment.cancellation_reason,
    cancelledAt: dbAppointment.cancelled_at,
    cancelledBy: dbAppointment.cancelled_by,
    rescheduledFrom: dbAppointment.rescheduled_from,
    rescheduleCount: dbAppointment.reschedule_count,
    paymentStatus: dbAppointment.payment_status,
    paymentAmount: dbAppointment.payment_amount,
    paymentCurrency: dbAppointment.payment_currency,
    paymentTransactionId: dbAppointment.payment_transaction_id,
    isRecurring: dbAppointment.is_recurring,
    recurrencePattern: dbAppointment.recurrence_pattern,
    parentSeriesId: dbAppointment.parent_series_id,
    remindersSent: dbAppointment.reminders_sent,
    confirmationToken: dbAppointment.confirmation_token,
    cancellationToken: dbAppointment.cancellation_token,
    rescheduleToken: dbAppointment.reschedule_token,
    createdAt: dbAppointment.created_at,
    updatedAt: dbAppointment.updated_at,
    // Relationships (simplified for API response)
    calendar: dbAppointment.calendars ? {
      id: dbAppointment.calendars.id,
      name: dbAppointment.calendars.name
    } : undefined,
    appointmentType: dbAppointment.appointment_types ? {
      id: dbAppointment.appointment_types.id,
      name: dbAppointment.appointment_types.name
    } : undefined,
    contact: dbAppointment.contacts ? {
      id: dbAppointment.contacts.id,
      firstName: dbAppointment.contacts.first_name,
      lastName: dbAppointment.contacts.last_name,
      email: dbAppointment.contacts.email,
      phone: dbAppointment.contacts.phone
    } : undefined
  }
}

function generateToken(): string {
  return uuidv4().replace(/-/g, '').substring(0, 24)
}

async function getNextRoundRobinUser(supabase: any, calendarId: string): Promise<string | null> {
  // Get active calendar members
  const { data: members } = await supabase
    .from('calendar_members')
    .select('user_id, priority_order, weight')
    .eq('calendar_id', calendarId)
    .eq('is_active', true)
    .order('priority_order', { ascending: true })

  if (!members || members.length === 0) {
    return null
  }

  // Get last assignment
  const { data: lastAssignment } = await supabase
    .from('round_robin_assignments')
    .select('last_assigned_user_id, assignment_counts')
    .eq('calendar_id', calendarId)
    .single()

  if (!lastAssignment) {
    // First assignment - use first member
    return members[0].user_id
  }

  // Find next user in rotation
  const lastIndex = members.findIndex((m: any) => m.user_id === lastAssignment.last_assigned_user_id)
  const nextIndex = (lastIndex + 1) % members.length
  const nextUserId = members[nextIndex].user_id

  // Update assignment tracking
  const counts = lastAssignment.assignment_counts || {}
  counts[nextUserId] = (counts[nextUserId] || 0) + 1

  await supabase
    .from('round_robin_assignments')
    .upsert({
      calendar_id: calendarId,
      last_assigned_user_id: nextUserId,
      last_assigned_at: new Date().toISOString(),
      assignment_counts: counts,
      updated_at: new Date().toISOString()
    })

  return nextUserId
}

function generateGoogleCalendarUrl(appointment: any, calendar: any): string {
  const startDate = new Date(appointment.start_time)
  const endDate = new Date(appointment.end_time)
  
  const formatDate = (d: Date) => d.toISOString().replace(/[-:]/g, '').replace(/\.\d{3}/, '')
  
  const params = new URLSearchParams({
    action: 'TEMPLATE',
    text: `${calendar.name} - Appointment`,
    dates: `${formatDate(startDate)}/${formatDate(endDate)}`,
    details: appointment.notes || '',
    location: appointment.meeting_location || appointment.meeting_link || ''
  })

  return `https://calendar.google.com/calendar/render?${params.toString()}`
}

function generateOutlookUrl(appointment: any, calendar: any): string {
  const startDate = new Date(appointment.start_time)
  const endDate = new Date(appointment.end_time)
  
  const params = new URLSearchParams({
    subject: `${calendar.name} - Appointment`,
    startdt: startDate.toISOString(),
    enddt: endDate.toISOString(),
    body: appointment.notes || '',
    location: appointment.meeting_location || appointment.meeting_link || ''
  })

  return `https://outlook.office.com/calendar/0/deeplink/compose?${params.toString()}`
}
